import React from "react";
import { injectIntl } from "react-intl";
import Context from "../../Context";
import { Helmet } from "react-helmet";
import { Alert, Empty, Spin } from "antd";
import { Link } from "react-router-dom";

class Search extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);

    this.state = {
      pending: false,
      results: [],
      q: "",
    };
  }

  init = () => {
    const searchParams = new URLSearchParams(document.location.search);
    const q = searchParams.get("q");
    if (this.state.q !== q) {
      this.setState({ q }, () => this.load());
    }
  };

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };

  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };

  componentDidMount() {
    this.init();
  }

  componentDidUpdate(prevProps) {
    this.init();
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.load();
    }
  }

  load = async () => {
    const { q } = this.state;
    let results = [];
    if (q) {
      this.pendingStart();
      results = await this.context.api.q(
        "/search",
        { text: q, entities: ["charge_point"] },
        { toArray: true }
      );
      this.pendingEnd();
      if (!results?.[0]?.id) results = [];
    }
    console.log(results);
    this.setState({ results });
  };

  m = (key, prefix = "search.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  render() {
    const { pending, results } = this.state;
    return (
      <div>
        <Helmet>
          <title>{this.m("search")}</title>
        </Helmet>
        <h2 className="title gx-mb-4">
          {this.m("search")}
          <span className="gx-ml-2 gx-text-muted gx-font-weight-light">
            {this.state.q}
          </span>
        </h2>

        <Spin spinning={pending}>
          {results.length ? (
            <div>
              <h4>{this.m("results")}:</h4>
              {results.map((item) => {
                return (
                  <div key={item.id} className="gx-mb-4 gx-mt-4 ">
                    <div className="gx-text-muted gx-font-weight-light">
                      {item.classlabel}
                    </div>
                    <Link to={`/crm/station/${item.id}/`}>
                      {item.name || item.label}
                    </Link>
                    <hr />
                  </div>
                );
              })}
            </div>
          ) : (
            <Empty />
          )}
        </Spin>
      </div>
    );
  }
}

export default injectIntl(Search);
